:root{
  --red: #e32d2c;
  --blue: #161b33;
  --transition: all 1.5s cubic-bezier(0.23, 1, 0.320, 1);
}

.cart-wrapper {
	width: 97%;
	max-width: 1200px;
	margin: 0 auto;
	float: none;
    margin-top: 5rem;
   
  
}

.shopping-cart{
  width: 750px;
  height: auto;
  margin: 5em auto;
  background: #FFF;
  box-shadow: 1px 2px 3px 0px rgba(0,0,0,0.10);
  border-radius: 0.5em;
  
  display: flex;
  flex-direction: column;


}

.cart-wrapper .heading{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 6rem;
  flex-wrap: wrap;


}

.title{
  height: 3.75em;
  border-bottom: 1px solid #E1E8EE;
  padding: 1.25em 1.75em;
  color: #5E6977;
  font-size: 1.125em;
  font-weight: 400;
}
.cart-item{
  /* padding: 1.25em 1.75em;
  height: 7.5em; */
  display: flex;
  align-items: center;
}
.item:nth-child(3) {
  border-top: 1px solid #E1E8EE;
  border-bottom: 1px solid #E1E8EE;
}
.card-item:nth-child(3) {
  border-top: 1px solid #E1E8EE;
  border-bottom: 1px solid #E1E8EE;
}
.cart-buttons{
  position: relative;
  padding-top: 1.75em;
  margin-right: 3.5em;
  
}

.delete-btn
{
  display: inline-block;
  cursor: pointer;
}
.delete-btn{
  width: 18px;
  height: 17px;
  background: url("https://designmodo.com/demo/shopping-cart/delete-icn.svg") no-repeat center;
}

.cart-image-container{
  margin-right: 50px;
  /* max-height: 700px;
  max-width: 605px; */
 
}



.card-image-container img{
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  width: 60px;
  display: none;
}

.cart-description{
  padding-top: 10px;
  margin-right: 60px;
  width: 300px;
}
.cart-description span{
  /* display: block; */
  font-size: 1em;
  color: #43484D;
  font-weight: 400;
}

.cart-description span:first-child{
  margin-bottom: 5px;
}
.cart-description span:last-child{
  font-weight: 300;
  margin-top: 8px;
  color: #86939E;
}

.cart-quantity{
  padding-top: 20px;
  margin-right: 60px;
  display: flex;
  align-items: center;
  
}


.cart-quantity input{
  -webkit-appearance: none;
  border: none;
  text-align: center;
  width: 32px;
  font-size: 1em;
  color: #43484D;
  font-weight: 300;
}

.cart-item button[class*=btn] {
  width: 30px;
  height: 30px;
  background-color: #E1E8EE;
  border-radius: 6px;
  border: none;
  cursor: pointer;
}
.minus-btn img{
  margin-bottom: 3px;
}
.plus-btn img{
  margin-top: 2px;
}

.cart-item button:focus,
.cart-item input:focus{
  outline: 0;
}

.cart-total-price{
  width: 83px;
  padding-top: 27px;
  text-align: center;
  font-size: 1em;
  color: #43484D;
  font-weight: 300;
}

.is-active{
  -webkit-animation-name: animate;
          animation-name: animate;
  -webkit-animation-duration: .8s;
          animation-duration: .8s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: steps(28);
          animation-timing-function: steps(28);
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes animate{
  0% {background-position: left;}
  50% {background-position: right;}
  100% {background-position: right;}
}
@keyframes animate{
  0% {background-position: left;}
  50% {background-position: right;}
  100% {background-position: right;}
}

