/* media queries */
@media (max-width: 800px) {
  .shopping-cart{
    width: 100%;
    height: auto;
    overflow: hidden;
  }
  .cart-item {
    height: auto;
    flex-wrap: wrap;
    justify-content: center;
  }
  .cart-image img{
    width: 50%;
  }
  .cart-image,
  .cart-quantity,
  .cart-description{
    width: 100%;
    text-align: center;
    margin: 6px 0;
    justify-content: center;
  }
  .cart-buttons{
    margin-right: 20px;
  }

  .cart-wrapper.heading{
    flex-flow: row wrap;
  }
}
